import React from "react";
import FeaturedTable from "./Editable";
import PropTypes from "prop-types";
import "./styles.css";

const Table = React.forwardRef((props, ref) => (
  <FeaturedTable {...props} ref={ref} />
));

Table.defaultProps = {
  editable: false,
  editTools: ["edit", "add"],
  editToolsConfig: {
    position: "bottom",
    itemStyle: { marginLeft: "5px" },
    editText: "",
    editIcon: "",
    addText: "",
    addIcon: "",
    deleteText: "",
    deleteIcon: "",
    okText: "",
    okIcon: "",
    cancelText: "",
    cancelIcon: ""
  },
  defaultAddCount: 1,
  isAppend: false,
  validateTrigger: "onSave",
  validateDelay: 300,
  allowSaveEmpty: false,
  dataControled: false,
  alwaysValidate: false
};

Table.propTypes = {
  /**  */
  editable: PropTypes.bool,
  /**  ['edit', 'add','delete',{icon:"",text:"",props:{},handler:Function},Function] addSingle: */
  editTools: PropTypes.array,
  /** { position: "bottom", itemStyle: { marginLeft: "5px" }, editText: "", editIcon: "", addText: "", addIcon: "", deleteText: "", deleteIcon: "", okText: "", okIcon: "", cancelText: "", cancelIcon: "" } */
  editToolsConfig: PropTypes.object,
  /**  */
  isAppend: PropTypes.bool,
  /**  */
  defaultAddCount: PropTypes.number,

  /**  (changedRows,newRows,editType)=>void
   * @param {Array} changedRows-
   * @param {Array} newRows-
   * @param {string} editType-;"edit":;"add":;"delete":
   */
  onEditSave: PropTypes.func,
  /** onEditSave */
  allowSaveEmpty: PropTypes.bool,
  /**  */
  validateTrigger: PropTypes.oneOf(["onChange", "onBlur", "onSave"]),

  /** false ()=>bool */
  onBeforeAdd: PropTypes.func,

  /**  (addedData, newData)=>void
   * @param {Array} addedData-
   * @param {Array} newRows-
   */
  onAdd: PropTypes.func,

  /**  (rowIndex)=>object
   * @param {numer} rowIndex
   * @returns {object} 
   */
  rowTemplate: PropTypes.func,

  /**  */
  onCancel: PropTypes.func,
  /** false */
  onBeforeEdit: PropTypes.func,

  /**  */
  onEdit: PropTypes.func,
  /**
   * ,false 
   */
  onBeforeDelete: PropTypes.func,
  /**  */
  onDelete: PropTypes.func,
  /**  */
  alwaysValidate: PropTypes.bool,
  /** onEditSaveonCancel */
  dataControled: PropTypes.bool
};

export default Table;
