import React from "react";
import PropTypes from "prop-types";

import Pagination from "antd/lib/pagination";
import Button from "antd/lib/button";
import "antd/dist/antd.css";
import "./styles.css";

class Pager extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      current: props.current || 1,
      pageSize: props.pageSize,
      total: props.total
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    let nextState = {};

    if ("current" in nextProps) {
      nextState.current = nextProps.current;
    }

    if ("pageSize" in nextProps) {
      nextState.pageSize = nextProps.pageSize;
    }

    if ("total" in nextProps) {
      nextState.total = nextProps.total;
    }

    if (Object.keys(nextState).length > 0) {
      return nextState;
    }

    return null;
  }

  onShowSizeChange = (pageIndex, pageSize) => {
    if (typeof this.props.onPageChange === "function") {
      this.props.onPageChange(pageIndex, pageSize);
    }
  };

  onRefresh = () => {
    let { current, pageSize } = this.state;

    if (typeof this.props.onRefresh === "function") {
      this.props.onRefresh(current, pageSize);
    }
  };

  onPageIndexChange = (pageIndex, pageSize) => {
    if (typeof this.props.onPageChange === "function") {
      this.props.onPageChange(pageIndex, pageSize);
    }
  };

  render() {
    let { pageSize, current, total } = this.state;

    let pagerAttr = {
      ...this.props,
      showSizeChanger: true,
      onShowSizeChange: this.onShowSizeChange,
      onChange: this.onPageIndexChange,
      pageSize,
      current,
      total
    };

    let refreshButton = (
      <Button
        style={{ verticalAlign: "middle" }}
        icon="reload"
        onClick={this.onRefresh}
      />
    );

    let start = pageSize * (current - 1) + 1;
    let end = pageSize * current;
    end = end > total ? total : end;

    let totalCount = total;

    if (total <= 0) {
      start = 0;
      end = 0;
      totalCount = 0;
    }

    let totalInfo = (
      <div className="tablex__pager__count">
         {start}-{end} {totalCount} 
      </div>
    );

    return (
      <div className="tablex__pager">
        <div className="tablex__pager__number">
          <Pagination {...pagerAttr} />
          {refreshButton}
        </div>
        {totalInfo}
      </div>
    );
  }
}

Pager.defaultProps = {
  current: 1,
  pageSize: 10,
  total: 0,
  pageSizeOptions: ["10", "20", "30", "40"],
  onPageChange: () => {}
};

Pager.propTypes = {
  /**  */
  current: PropTypes.number.isRequired,
  /**  */
  pageSize: PropTypes.number.isRequired,
  /**  */
  total: PropTypes.number,
  /**  */
  onPageChange: PropTypes.func,
  /**  */
  pageSizeOptions: PropTypes.array
};

export default Pager;
